import React from 'react'
import './index.less'
import { Link } from 'react-router-dom'
import { Icon, Input, Button, notification, Select, Upload, Modal } from 'antd';
import { baseUrl } from '../../config';
import axios from 'axios'

function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

export default class ReposImgUpload extends React.Component {
    state = {
        id_pyq: '',
        previewVisible: false,
        previewImage: '',
        fileList: [
            // {
            //     uid: '-1',
            //     name: 'xxx.png',
            //     status: 'done',
            //     url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
            // },
        ],
    };

    handleCancel = () => this.setState({ previewVisible: false });

    handlePreview = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
        });
    };

    handleChange = ({ fileList }) => this.setState({ fileList });
    componentDidMount = () => {
        this.setState({
            id_pyq: localStorage.id_pyq,
            token: localStorage.token,
        })

    }
    render() {
        const { id_pyq, token, previewVisible, previewImage, fileList } = this.state;
        const uploadButton = (
            <div>
                <Icon type="plus" />
                <div className="ant-upload-text">点击上传</div>
            </div>
        );
        return (
            <div className="clearfix">
                <h2>上传动态图片</h2>
                <h4>最多9张</h4>
                <Upload
                    action={`${baseUrl}/api/upload_pyq_data`}
                    listType="picture-card"
                    fileList={fileList}
                    onPreview={this.handlePreview}
                    onChange={this.handleChange}
                    data={{ id_pyq, token, type: 'img' }}
                >
                    {fileList.length >= 9 ? null : uploadButton}
                </Upload>
                <span>请等待上传进度条结束</span>

                < Link to={`/seller/${localStorage.login_id}`}>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        确认发送
                    </Button>
                </Link>
                <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                    <img alt="example" style={{ width: '100%' }} src={previewImage} />
                </Modal>

            </div>
        );
    }
}
