import React from 'react'
import './index.less'
import { Link } from 'react-router-dom'
import { Form, Icon, Input, Button, notification, Select } from 'antd';
import { baseUrl } from '../../config';
import axios from 'axios'
class NormalLoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_pyq: '',
        }
    }
    componentDidMount() {
        // 获取货号
        axios.get(
            baseUrl + '/api/before_generate_pyq',
            {
                params: {
                    token: localStorage.token,
                }
            })
            .then((Response) => {
                console.log(Response.data.id_pyq);
                localStorage.id_pyq = Response.data.id_pyq
                this.setState({
                    id_pyq: Response.data.id_pyq,
                })
            })
    }
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                // 发送文字
                axios.get(
                    baseUrl + '/api/generate_pyq',
                    {
                        params: {
                            token: localStorage.token, //用户token
                            id_pyq: this.state.id_pyq,  //货号
                            user_text: values.user_text, //描述
                        }
                    })
                    .then((Response) => {
                        // 发送链接
                        axios.post(baseUrl + '/api/upload_pyq_data', {
                            token: localStorage.token, //用户token
                            id_pyq: this.state.id_pyq,  //货号
                            type: 'url',
                            url: values.url, //描述
                        })
                            .then(function (response) {
                                console.log(response);
                                notification.open({
                                    message: '提交成功',
                                    description: '正在自动跳到下一页...',
                                    placement: 'bottomLeft',
                                    icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
                                });
                                setTimeout(() => {
                                    document.location = `#/seller/${localStorage.login_id}`
                                }, 500);

                            })
                            .catch(function (error) {
                                console.log(error);
                            });
                        console.log(Response.data);
                    })
            }
        });
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form" >
                <h2>分享链接到动态</h2>
                <Form.Item>
                    {getFieldDecorator('user_text', {
                        rules: [{ required: true, message: '请输入动态内容' }],
                    })(
                        <Input
                            placeholder="动态内容"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('url', {
                        rules: [{ required: true, message: '请输入要分享的链接' }],
                    })(
                        <Input
                            placeholder="链接"
                        />,
                    )}
                </Form.Item>


                <Form.Item>
                    {/* < Link to='/admin/upload/img'> */}
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        发送
                    </Button>
                    {/* </Link> */}
                </Form.Item>
            </Form>
        )
    }

}

export const ReposUrlUpload = Form.create({ name: 'normal_login' })(NormalLoginForm);
