import React from 'react'
import './index.less'
import { Link } from 'react-router-dom'
import { Icon, Input, Button, notification, Select, Upload, Modal } from 'antd';
import { baseUrl } from '../../config';
import axios from 'axios'

function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

export default class GgImgUpload extends React.Component {
    state = {
        method: '',
        id_gg: '',
        previewVisible: false,
        previewImage: '',
        fileList: [
            // {
            //     uid: '-1',
            //     name: 'xxx.png',
            //     status: 'done',
            //     url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
            // },
        ],
    };

    handleCancel = () => this.setState({ previewVisible: false });

    handlePreview = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
        });
    };

    handleChange = ({ fileList }) => this.setState({ fileList });
    componentDidMount = () => {
        this.setState({
            token: localStorage.token,
            id_seller: localStorage.id_seller,
            id_gg: localStorage.id_gg,
            method: localStorage.id_gg === undefined ? 'create' : 'update'
        })

    }
    render() {
        const { id_seller, token, id_gg, method, previewVisible, previewImage, fileList } = this.state;
        const uploadButton = (
            <div>
                <Icon type="plus" />
                <div className="ant-upload-text">上传图片</div>
            </div>
        );
        return (
            <div className="clearfix">
                <h2>上传一张公告图片</h2>
                <Upload
                    action={`${baseUrl}/api/curd_gg`}
                    listType="picture-card"
                    fileList={fileList}
                    onPreview={this.handlePreview}
                    onChange={this.handleChange}
                    data={{ id_seller, token, id_gg, method }}
                >
                    {fileList.length >= 1 ? null : uploadButton}
                </Upload>
                {fileList.length >= 1
                    ?
                    <div>
                        <span>请等待上传进度条结束</span>
                        < Link to={`/seller/${localStorage.login_id}`}>
                            <Button type="primary" htmlType="submit" className="login-form-button">
                                上传结束，返回店铺主首页
                    </Button>
                        </Link>
                    </div>
                    : null}
                <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                    <img alt="example" style={{ width: '100%' }} src={previewImage} />
                </Modal>

            </div>
        );
    }
}
