import React from 'react'
import './index.less'
import axios from 'axios'
import { Route, Link } from 'react-router-dom'
import { Avatar, Carousel, Spin, Button, Modal } from 'antd';
import Goods from './goods/index';
import { baseUrl } from '../config';
import Repos from './repos/index';
import { Zmage } from 'react-zmage';
export default class Seller extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            seller_info: [],
            products: [],
            login: false,
            visible: false,
            visibleHow2Share: false,
            gg: [],
        }
    }
    componentWillMount() {

        axios.get(
            baseUrl + '/api/get_seller_info',
            {
                params: {
                    id_seller: this.props.match.params.id,
                }
            })
            .then((Response) => {
                //return data数据
                console.table(Response.data);
                this.setState(Response.data)
                console.table(this.state);

            })
            .catch((err) => {
                console.log(err);
            })
        axios.get(
            baseUrl + '/api/get_products',
            {
                params: {
                    id_seller: this.props.match.params.id,
                }
            })
            .then((Response) => {
                //return data数据
                console.table('>>>res', Response.data);
                // 追加products
                this.setState({
                    products: Response.data.products,
                })
                console.table('>>>goods state', this.state);
                // 判断是否登录
                if (this.props.match.params.id === localStorage.login_id) {
                    this.setState({ login: true, })
                    // 公告id
                    if (this.state.gg[0].id !== undefined) {
                        localStorage.id_gg = this.state.gg[0].id
                    } else {
                        localStorage.id_gg = undefined
                    }

                    localStorage.id_seller = this.props.match.params.id
                }
            })
            .catch((err) => {
                console.log(err);
            })
        console.log('final', this.state);

    }
    showRepos = () => {
        this.setState({ visible: true })
    }
    showHow2Share = () => {
        this.setState({ visibleHow2Share: true })
    }
    handleCancel = () => {
        this.setState({ visible: false })
    }
    handleCancelHow2Share = () => {
        this.setState({ visibleHow2Share: false })
    }
    render() {
        return (
            <div>
                <div className="header-new">
                    {this.state.seller_info.length ?
                        <div>

                            <Avatar size={64} src={this.state.seller_info[0].seller_img.includes('http') ? this.state.seller_info[0].seller_img : baseUrl + this.state.seller_info[0].seller_img || ''} className='avatar'></Avatar>
                            <h2 className="seller-name">
                                『{this.state.seller_info[0].sellerName || ''}』
                            </h2>
                            <br></br>
                            <span>
                                店铺号:{this.props.match.params.id}
                            </span>
                            {this.state.login
                                ? < Link to='/admin/seller/upload'>设置店铺资料</Link>
                                : null
                            }
                        </div>
                        : <Spin />
                    }
                </div>
                {this.state.login ?
                    <div className='admin-board'>
                        < Link to='/admin/gg/upload/img'>
                            <Button type="primary" className="login-form-button">
                                发新公告
                            </Button>
                        </Link>
                        < Link to='/admin/upload'>
                            <Button type="primary" className="login-form-button">
                                发新商品
                            </Button>
                        </Link>
                        <Link onClick={this.showRepos}>
                            <Button type="primary" className="login-form-button">
                                发新动态
                            </Button>
                        </Link>
                        <Modal
                            visible={this.state.visible}
                            onOk={this.handleOk}
                            onCancel={this.handleCancel}
                        >
                            < Link to='/admin/repos/upload/url'>
                                <Button type="primary" htmlType="submit" className="login-form-button">
                                    分享新链接
                            </Button>
                            </Link>
                            <br></br>
                            <br></br>
                            < Link to='/admin/repos/upload'>
                                <Button type="primary" htmlType="submit" className="login-form-button">
                                    分享图片动态
                            </Button>
                            </Link>
                        </Modal>
                        <Link onClick={this.showHow2Share}>
                            <Button type="primary" className="login-form-button">
                                分享店铺
                            </Button>
                        </Link>
                        <Modal
                            visible={this.state.visibleHow2Share}
                            onOk={this.handleOkHow2Share}
                            onCancel={this.handleCancelHow2Share}
                        >
                            点击右上角的上个点
                            <img src='http://ww1.sinaimg.cn/large/006tNc79gy1g46i524kd7j30im0hyt9r.jpg' width='100%' alt=''></img>
                        </Modal>
                    </div>
                    : null
                }
                <div className='gg'>
                    <div>公告</div>
                    {this.state.gg.length === 0
                        ? <h3>暂无公告</h3>
                        : <img src={`${baseUrl}${this.state.gg[0].content}`} alt=''></img>
                    }
                </div>
                {/* <div>
                    < Carousel effect="fade" autoplay>
                        {this.state.products.map(p =>
                            p.productDatas.length !== 0 ?
                                <img className='carousel-img' src={`${baseUrl}${p.productDatas[0]}`} alt='' />
                                : ''
                        )}
                    </Carousel>
                </div> */}
                {/* 商品/动态 导航栏 */}
                <div>
                    <ul className="vnavbar-container">
                        <li className='vnavbar-list'>
                            <Link to={`${this.props.match.url}`} className={this.props.location.pathname.includes('repos') ? '' : 'active'}>商品</Link>
                        </li>
                        <li className='vnavbar-list'>
                            <Link to={`${this.props.match.url}/repos`} className={this.props.location.pathname.includes('repos') ? 'active' : ''}>动态</Link>
                        </li>
                    </ul>
                </div >
                {/* 商品/动态 */}
                <div className=''>
                    <Route exact path={this.props.match.url} render={() => (
                        <Goods id={this.props.match.params.id} login={this.state.login}></Goods>
                    )} />
                    <Route path={`${this.props.match.url}/repos`} render={() => (
                        <Repos id={this.props.match.params.id} login={this.state.login}></Repos>
                    )} />
                </div>
            </div >

        )
    }
}