import React from 'react'
import './index.less'
import axios from 'axios'
import { baseUrl } from '../../config';
import { Avatar, Icon, notification } from 'antd';
import Zmage from 'react-zmage'
export default class Repos extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            moments: [],
            lastProduct: '',
            seller_info: '',
        }
    }
    componentDidMount() {
        this.getAvartar(this.props.id)
        this.getMoments(this.props.id)

    }
    getAvartar = (id) => {
        axios.get(
            baseUrl + '/api/get_seller_info',
            {
                params: {
                    id_seller: id,
                }
            })
            .then((Response) => {
                this.setState({
                    seller_info: Response.data.seller_info,
                })
                console.table('res', this.state);


            })
            .catch((err) => {
                console.log(err);
            })
    }
    getMoments = (id) => {
        axios.get(
            baseUrl + '/api/get_seller_pyq',
            {
                params: {
                    id_seller: id,
                }
            })
            .then((Response) => {
                console.table('res', Response.data);
                this.setState({
                    moments: Response.data.pyqs,
                })
                console.table('state', this.state);

            })
            .catch((err) => {
                console.log(err);
            })
    }
    delete = (id) => {
        axios.get(
            baseUrl + '/api/delete_row',
            {
                params: {
                    token: localStorage.token, //用户token
                    type: 'pyq',  //货号
                    id_inf: id, //描述
                }
            })
            .then((Response) => {
                notification.open({
                    message: '删除成功',
                    description: '正在刷新...',
                    placement: 'bottomLeft',
                    icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
                });
                setTimeout(() => {
                    document.location.reload()
                }, 500);
            })
    }
    render() {
        return (
            <div className='repos'>
                <ul className="vproductShow-card-container">
                    {this.state.moments.map(m =>
                        <li className="moments-card" id={m.id}>
                            {
                                this.state.seller_info.length
                                    ? <div className='seller'>
                                        <Avatar size={54} shape='square' src={`${baseUrl}/${this.state.seller_info[0].seller_img}`} className='avatar'></Avatar>
                                        <span className='seller-name'>{this.state.seller_info[0].sellerName}</span>
                                    </div>
                                    : ''
                            }

                            <div className='moments'>
                                <p>{m.user_text}</p>
                                {/* 图片 */}
                                {m.pyq_imgs !== undefined
                                    ? <div className='repos-imgs'>
                                        {m.pyq_imgs.map(i => {
                                            return <Zmage zIndex={1000} className={m.pyq_imgs.length > 1 ? 'repos-img-multy' : 'repos-img-single'} src={baseUrl + i} alt=''></Zmage>
                                        })
                                        }
                                    </div>
                                    : null
                                }
                                {/* 链接 */}
                                {m.pyq_urls !== undefined
                                    ? <div className='link-shit'>
                                        <a href={m.pyq_urls[0].includes('http://') || m.pyq_urls[0].includes('https://') ? m.pyq_urls[0] : `http://${m.pyq_urls[0]}`}>
                                            <Icon type="link" /> {m.pyq_urls[0]}
                                        </a>
                                    </div>
                                    : null
                                }
                            </div>
                            {this.props.login ?
                                <button onClick={this.delete.bind(this, m.id)}>删除<Icon type="delete" /></button>
                                : null
                            }
                        </li>
                    )}
                </ul>
            </div>
        )

    }
}