import React from 'react'
import './index.less'
import { Link } from 'react-router-dom'
import { Form, Icon, Input, Button, notification, Select } from 'antd';
import { baseUrl } from '../../config';
import axios from 'axios'
class NormalLoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_pyq: '',
        }
    }
    componentDidMount() {

    }
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                // 新建条目
                axios.post(
                    baseUrl + '/api/update_seller_data',
                    {
                        token: localStorage.token, //用户token
                        type: 'sellerName',
                        data: values.seller_name, //描述
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        setTimeout(() => {
                            document.location = `#/admin/seller/upload/img`
                        }, 500);
                    })
            }
        });
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form" >
                <h2>设置店铺名称</h2>
                <Form.Item>
                    {getFieldDecorator('seller_name', {
                        rules: [{ required: true, message: '请输入店铺名称～' }],
                    })(
                        <Input
                            placeholder="店铺名称"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {/* < Link to='/admin/upload/img'> */}
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        上传店铺头像
                    </Button>
                    {/* </Link> */}
                </Form.Item>
            </Form>
        )
    }

}

export const SellerUpload = Form.create({ name: 'normal_login' })(NormalLoginForm);
