import React from 'react'
import './index.less'
import { Link } from 'react-router-dom'
import { Form, Icon, Input, Button, notification, Select } from 'antd';
import { baseUrl } from '../../config';
import axios from 'axios'
class NormalLoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_pyq: '',
        }
    }
    componentDidMount() {
        // 获取货号
        axios.get(
            baseUrl + '/api/before_generate_pyq',
            {
                params: {
                    token: localStorage.token,
                }
            })
            .then((Response) => {
                console.log(Response.data.id_pyq);
                localStorage.id_pyq = Response.data.id_pyq
                this.setState({
                    id_pyq: Response.data.id_pyq,
                })
            })
    }
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                // 新建条目
                axios.get(
                    baseUrl + '/api/generate_pyq',
                    {
                        params: {
                            token: localStorage.token, //用户token
                            id_pyq: this.state.id_pyq,  //货号
                            user_text: values.user_text, //描述
                        }
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        setTimeout(() => {
                            document.location = `#/admin/repos/upload/img`
                        }, 500);
                    })
            }
        });
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form" >
                <h2>发布新动态</h2>
                <Form.Item>
                    {getFieldDecorator('user_text', {
                        rules: [{ required: true, message: '请输入动态～' }],
                    })(
                        <Input
                            placeholder="新动态"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {/* < Link to='/admin/upload/img'> */}
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        添加图片
                    </Button>
                    {/* </Link> */}
                </Form.Item>
            </Form>
        )
    }

}

export const ReposUpload = Form.create({ name: 'normal_login' })(NormalLoginForm);
