import React from 'react'
import './index.less'
import { Link } from 'react-router-dom'
import { Form, Icon, Input, Button, notification, Select } from 'antd';
import { baseUrl } from '../../config';
import axios from 'axios'
class NormalLoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_product: '',
        }
    }
    componentDidMount() {
        // 获取货号
        axios.get(
            baseUrl + '/api/before_generate_products',
            {
                params: {
                    token: localStorage.token,
                }
            })
            .then((Response) => {
                console.log(Response.data.id_product);
                localStorage.id_product = Response.data.id_product
                this.setState({
                    id_product: Response.data.id_product,
                })
            })
    }
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                let urls = []
                if (values.tburl !== undefined) {
                    urls.push(
                        {
                            type: 'tburl',
                            content: values.tburl,
                        })
                }
                if (values.taok !== undefined) {
                    urls.push(
                        {
                            type: 'taok',
                            content: values.taok,
                        })
                }
                if (values.jd !== undefined) {
                    urls.push(
                        {
                            type: 'jd',
                            content: values.jd,
                        })
                }
                if (values.pdd !== undefined) {
                    urls.push(
                        {
                            type: 'pdd',
                            content: values.pdd,
                        })
                }
                console.log(urls);
                // 新建条目
                axios.get(
                    baseUrl + '/api/generate_products',
                    {
                        params: {
                            token: localStorage.token, //用户token
                            id_product: this.state.id_product,  //货号
                            product_name: values.product_name,  //名字
                            price: values.price, //价格
                            infs: values.infs, //描述
                            urls: JSON.stringify(urls), //链接
                        }
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        notification.open({
                            message: '提交成功',
                            description: '正在自动跳到下一页...',
                            placement: 'bottomLeft',
                            icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
                        });
                        setTimeout(() => {
                            document.location = `#/admin/upload/img`
                        }, 500);
                    })
            }
        });
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form" >
                <h2>发布新商品</h2>
                <Form.Item>
                    {getFieldDecorator('product_name', {
                        rules: [{ required: true, message: '请输入商品名～' }],
                    })(
                        <Input
                            placeholder="商品名"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('price', {
                        rules: [{ required: true, message: '请输入商品价格' }],
                    })(
                        <Input
                            placeholder="请输入商品价格"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('infs', {
                        rules: [{ required: true, message: '请输入商品描述' }],
                    })(
                        <Input
                            placeholder="商品描述"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('tburl', {
                        rules: [],
                    })(
                        <Input
                            placeholder="淘宝链接，选填"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('taok', {
                        rules: [],
                    })(
                        <Input
                            placeholder="淘口令，选填"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('jd', {
                        rules: [],
                    })(
                        <Input
                            placeholder="商品京东链接，选填"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('pdd')(
                        <Input
                            placeholder="拼多多链接，选填"
                        />,
                    )}
                </Form.Item>

                <Form.Item>
                    {/* < Link to='/admin/upload/img'> */}
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        下一步
                    </Button>
                    {/* </Link> */}
                </Form.Item>
            </Form>
        )
    }

}

export const ProductsUpload = Form.create({ name: 'normal_login' })(NormalLoginForm);
